/* resume button styles */



.btn {
  float: left;
  margin-left:20% ;
  background-color: transparent;
    border: 1px solid #e74c3c; 
    color: #e74c3c; 
    cursor: pointer;
 
    font-size: 1rem;
    line-height: 1;
    padding: 1em 1em;
    text-decoration: none;
    text-align: center;
  
    font-weight: 700;
  }
  .btn:hover, .btn:focus {
    color: #fff;
    outline: 0;
  }
  .fifth {
    border-color: #6b2d5c;
    border-radius: 5px;
    color: #6b2d5c;
    position: relative;
    overflow: hidden;
    z-index: 1;
    -webkit-transition: color 150ms ease-in-out;
    transition: color 150ms ease-in-out;
  }
  .fifth:after {
    content: '';
    position: absolute;
    display: block;
    top: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 0;
    height: 100%;
    background: #6b2d5c;
    z-index: -1;
    -webkit-transition: width 150ms ease-in-out;
    transition: width 150ms ease-in-out;
  }
  .fifth:hover {
    color: #fff;
  }
  .fifth:hover:after {
    width: 110%;
  }
  
