.sm a
{
  display: inline-block;
  font-size: 40px;
  color: #6b2d5c;
  width: 100px;
  height: 100px;
  line-height: 100px;
  text-align: center;
  margin: 0 16px;
  transition: .4s linear;
  position: relative;
  box-shadow:0 0 0 1px rgba(0,0,0,0);
}

.sm a:hover
{
  transform: scale(.8);
}



