.ProjectsContainer{
    
    display: flex;
    justify-content: space-between;
    padding: 30px;

}
.projectsContainerWraper{
    width:90%;
    margin: auto;
    height: 100%;
    background-color: transparent;

    /* background-color: #343641; */
    /* padding-top: 100px; */
}
.projectsContainerWraper h1{
    padding-top: 70px;
}
@media screen and (max-width:950px){

    .ProjectsContainer{
        flex-direction: column;
        align-items: center;
        height: auto;
    }
    .ProjectsContainer .cardContainer{
        width:80%;
        margin:  5% 0;
        box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);

    }
