.cardContainer {
    /* Add shadows to create the "card" effect */
    /* box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2); */
    box-shadow: 10px 9px 8px 0 rgb(74 73 88);
  
    transition: 0.3s;
    margin: 30px 0;
  }
  .cardImageConainer{
    height:70%;

  }
  /* On mouse-over, add a deeper shadow */
  .cardContainer:hover {
    /* box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2); */
    box-shadow: 8px 8px 11px 3px rgba(0,0,0,0.2);
  
  }
  
  /* Add some padding inside the card container */
  .container {
    height: 25%;
    padding:5% 0px;

    /* padding: 2px 16px; */
  }
  .cardContainer{
      width:30%;
      height: 300px;
      background-color: #56686D;
      /* overflow: hidden; */

  }
  .cardContainer img{
      height: 100%;
  }