
.navContainer{
  position: fixed;
  z-index: 5;
  display: flex;
  width: 100%;
  justify-content: space-between;
  /* background-color: #343641; */
  background: rgb(73,72,87);
    background: linear-gradient(90deg, rgba(73,72,87,1) 0%, rgba(182,190,191,1) 50%, rgba(193,202,201,1) 56%, rgba(193,202,201,1) 100%);
   


}
.navContainer .res{
  padding:8px 0;

  width:50%;
  display: flex;
  /* justify-content: space-between; */
}
.navContainer ul a, .navContainer ul p{

  color: #733061;
  

}
.navContainer ul a:hover, .navContainer ul p:hover{
/* background-color: #733061; */
color: black;


}
.navContainer ul{
  padding:8px 0;
  margin-right: 6%;
  width:40%;
  display: flex;
  justify-content: space-between;
  list-style-type: none;
}
.navContainer .logo{
  position: absolute;
  top: 4px;
  margin-left: 5%;
  /* top: 0; */

}
.navContainer .res .title{
  margin-left: 45%;
  font-weight: bold;
}
.navContainer .res .icon{
  display: none;
}
.navContainer  a{
  text-decoration:none;

}
.res .logo img{
  height:30px;
}


@media screen and (max-width:670px){
  .navContainer{
    flex-flow: column;
  }
  .navContainer ul {
    display: block;
    margin: 0;
    width:100%;
    flex-flow: wrap;
    text-align: center;
    margin: 50px 0;
  }
  .navContainer ul a{
    display: block;
    padding: 10px 0;
    /* color: #c8e981; */

  }
  .navContainer ul a:hover{
    background-color: #30323d;
    /* color: #9bc53d; */

  }
  /* .navContainer .res .title{
    margin: 0;
  } */
  #show{
    display: block;
  }
  #notShow{
    display: none;
  }
  .navContainer .res .logo{

    margin-left: 5%;
  }
  .navContainer .res{
    margin: 0;
    width:100%;
    justify-content: space-between;
    padding: 12px 0;
  }
  .navContainer .res .icon{
    display: inline;
    margin-right: 5%;
    /* color: #9bc53d; */
    
  }
  .res .logo img{
  height: 40px;
  }
}
