h1,
h2,
h3,
h4,
h5,
h6,
p {
		font-family: 'Open Sans' sans-serif;
		color: #FBFBFB;
		margin: 0;
}
.contact-wrapper{
	/* background-color: #343641;
	 */
	 background-color: #92a4ae;

}
.wrapper{
    width: 90%;
    margin:auto;
    /* background-color: #30323d; */
	padding: 10% 0;
	
	
}


.contact__container {
		display: block;
		border: 10px solid #343641;
		border-radius: 10px;
		margin: 0 auto;
		padding: 0 0 10px 10px;
		max-width: 330px;
		background-color: #343641;
}

.contact__title {
		display: inline-block;
}

.contact__title h2 {
		padding: 0 10px 0 0px;
		margin: 5px 0;
		color: #FBFBFB;
}

.contact__form {
		padding: 10px 10px 0 0;
}

hr {
		width: 100%;
		display: block;
		margin: 5px 0;
		height: 5px;
		border: 0;
		background-color: #FBFBFB;
}

input,
textarea {
		width: 100%;
		border: 5px solid #FFFFFF;
		border-radius: 5px;
		resize: none;
		padding: 0 5px;
		box-sizing: border-box;
		font-family: 'Open Sans' sans-serif;
		font-size: 18px;
        color: #25606f;
        background-color:  #7e856e;
}

.contact-wrapper button {
		padding: 0;
		height: 40px;
		width: 100%;
}

input:focus,
textarea:focus {
		outline: none;
}

.contact__form-input {
        margin: 5px 0 10px 0;
        background-color: #7e856e;
}

.contact__form-textarea {
		margin: 5px 0;
		width: 100%;
		height: 100px;
}

.contact-wrapper button {
		width: 100%;
		height: 50px;
		background-color: #25606f;
		border: 0;
		color: #FBFBFB;
		border: 5px solid #25606f;
		border-radius: 5px;
		margin: 10px 0 0 0;
		font-family: 'Open Sans' sans-serif;
		font-size: 22px;
}

.contact-wrapper button :hover {
		background-color: #103741;
		border: #064858;
}